import {createStore} from 'vuex'
const store = createStore({

    state() {
        return {
            // Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú Ï†ëÍ∑ºÍ∞ÄÎä•
            todos: [],
            name:'',
            deleted:{}
        }
    },
    mutations: {
        // state Î≥ÄÏàòÎäî Ïô∏Î∂Ä Ïª¥Ìè¨ÎÑåÌä∏ÏóêÏÑú ÏàòÏ†ï Î∂àÍ∞ÄÎä•ÌïòÎØÄÎ°ú
        // mutation ÎÇ¥ Ï†ïÏùòÎêú Ìï®ÏàòÎ•º Ïù¥Ïö©Ìï¥Ïïº ÏÉÅÌÉúÏùò Î≥ÄÍ≤Ω Í∞ÄÎä•ÌïòÎã§
        addTodo(state, content) {
            state.todos.push(content)
        },
        delTodo(state, idx){
            state.deleted = state.todos[idx] //ÏÇ≠Ï†úÎêú Ìï≠Î™© Î≥¥Í¥Ä
            state.todos.splice(idx,1)
        },
        addName(state, name){
          state.name = name
        },
        changeCheck(state,obj){
            state.todos[obj.idx].completed = obj.data;
        },
        editContent(state,obj){
            state.todos[obj.idx].content = obj.context;
        },
        initData(state){
            state.todos = [];
        },
        restore(state){
            state.todos.push(state.deleted)
        },
        sortItem(state,selectedSort){
            if (selectedSort.value === "date-desc") {
                const todos = state.todos
                todos.sort(function(a, b) {
                    return b.time - a.time;
                })
                state.todos = todos
            } else if (selectedSort.value === "date-asc") {
                const todos = state.todos
                todos.sort(function(a, b) {
                    return a.time - b.time;
                })
                state.todos = todos
            }

        }
    }
})

export default store